    opacity: 0; }

/********** move-from-top **********/
.mfp-move-from-top {
  /* start state */
  /* animate in */
  /* animate out */ }
  .mfp-move-from-top .mfp-content {
    vertical-align: top; }
  .mfp-move-from-top .mfp-with-anim {
    transition: all 0.2s ease-in-out;
    transform: translateY(-100px);
    opacity: 0; }
  .mfp-move-from-top.mfp-bg {
