/******************************
Animation
******************************/
@keyframes freeRotateY {
  0% {
    transform: translateZ(10000px) rotateY(0deg); }
  50% {
    transform: translateZ(10000px) rotateY(180deg); }
  100% {
    transform: translateZ(10000px) rotateY(360deg); } }

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0); }
  40% {
    transform: translateY(-8px); }
  60% {
    transform: translateY(-4px); } }

