/********** FORM STRUCTURE **********/
.formRow {
  position: relative;
  width: 100%;
  margin-bottom: 10px;
  /*** specific style ***/
  /*** form content ***/
  /*** checkBoxRow ***/
  /*** radioRow ***/
  /*** upload / search (not yet done)***/ }
  .formRow.noLabel label.inputLabel {
    display: none; }
  .formRow.noLabel .formContent {
    margin-left: 0; }
  .formRow.blockItem label.inputLabel {
    position: relative;
    display: block;
    width: 100%; }
  .formRow.blockItem .formContent {
    margin-left: 0; }
  .formRow label.inputLabel {
    position: absolute;
    box-sizing: border-box;
    top: 0;
    left: 0;
    padding: 5px;
    width: 100px; }
    .formRow label.inputLabel span.text {
      display: inline-block;
      *display: inline;
      *zoom: 1;
      vertical-align: middle; }
  .formRow .formContent {
    position: relative;
    display: block;
    box-sizing: border-box;
    margin-left: 100px;
    min-height: 30px; }
  .formRow.checkBoxRow input[type="checkbox"] {
    display: none; }
  .formRow.checkBoxRow label.checkboxLabel {
    position: relative;
    display: block; }
    .formRow.checkBoxRow label.checkboxLabel span.checkIco {
      position: absolute;
      display: block;
      box-sizing: border-box;
      width: 20px;
      height: 20px;
      border: 1px solid #000000;
      top: 0;
      left: 0;
      cursor: pointer;
      background-color: #FFFFFF; }
      .formRow.checkBoxRow label.checkboxLabel span.checkIco .icon-form-check-tick {
        position: absolute;
        display: block;
        opacity: 0;
        transform: scale(1.5);
        width: 12px;
        height: 12px;
        top: 50%;
        left: 50%;
        margin-left: -6px;
        margin-top: -6px;
        background-color: #000000;
        transition: all 0.25s; }
      .formRow.checkBoxRow label.checkboxLabel span.checkIco.selected .icon-form-check-tick {
        opacity: 1;
        transform: scale(1); }
    .formRow.checkBoxRow label.checkboxLabel span.checkboxContent {
      position: relative;
      display: block;
      margin-left: 30px;
      text-align: left;
      cursor: pointer; }
      .formRow.checkBoxRow label.checkboxLabel span.checkboxContent a {
        text-decoration: underline; }
  .formRow.radioRow input[type="radio"] {
    display: none; }
  .formRow.radioRow label.radioLabel {
    position: relative;
    display: inline-block;
    *display: inline;
    *zoom: 1;
    cursor: pointer;
    margin-right: 20px; }
    .formRow.radioRow label.radioLabel span.checkIco {
      position: relative;
      display: inline-block;
      *display: inline;
      *zoom: 1;
      vertical-align: middle;
      box-sizing: border-box;
      width: 16px;
      height: 16px;
      border: 1px solid #000000;
      background-color: #FFFFFF;
      border-radius: 50%; }
      .formRow.radioRow label.radioLabel span.checkIco .icon {
        position: absolute;
        display: block;
        top: 50%;
        left: 50%;
        width: 8px;
        height: 8px;
        margin-left: -4px;
        margin-top: -4px;
        border-radius: 50%;
        opacity: 0;
        transform: scale(1.5);
        transition: all 0.25s; }
      .formRow.radioRow label.radioLabel span.checkIco.selected .icon {
        opacity: 1;
        transform: scale(1);
        background-color: #000; }
  .formRow .searchBtn,
  .formRow .uploadFormBtn {
    position: absolute;
    height: 30px;
    text-align: center;
    background-color: #000000;
    color: #FFFFFF;
    top: 0;
